  background-color: #fff;
  font-weight: 400;
  line-height: 1.5;
}
.header {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  padding: 4em 0;
  margin-bottom: 7vw;
}
.header__logo {
  grid-column: 2;
  color: #1b1b1b;
  font-size: 40px;
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: -1.5px;
}
.main {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.main__title {
  grid-column: 2/12;
  font-size: 80px;
  font-size: 5rem;
  line-height: 1.15;
  letter-spacing: -1px;
}
